/*
BASE RESET
*/
* {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline; }

html {
  -webkit-text-size-adjust: 100%;
  height: 100%; }

body {
  height: 100%;
  line-height: 1; }

h1, h2, h3, h4, h5, h6 {
  font-weight: normal; }

nav {
  display: block; }
  nav ol, nav li {
    list-style-type: none; }
  nav a {
    display: block;
    text-decoration: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

img {
  height: auto;
  vertical-align: bottom; }

em {
  font-style: normal; }

/*
BASE SETTINGS
*/
body {
  letter-spacing: .05em;
  font-size: 12px;
  font-family: "Noto Sans Japanese", sans-serif;
  line-height: 2;
  color: #000; }

body {
  background: #fff; }

a {
  color: #189CA7; }

/*
BASE ASSETS
*/
.cf {
  *zoom: 1; }
  .cf:before, .cf:after {
    content: "";
    display: table; }
  .cf:after {
    clear: both; }

.is_all_hide {
  display: none; }

.is_pc_hide {
  display: none; }
  @media screen and (max-width: 1024px) {
    .is_pc_hide {
      display: inherit; } }
